.button{display:flex;align-items:center;width:fit-content;text-align:center;border-radius:5px;padding:5px;color:inherit;font-weight:700;cursor:pointer;transition:all .3s;text-align:center;font-family:Roboto}.red{color:#fff;background-color:#c0392b}.red:hover{background-color:#e74c3c}.green{color:#fff;background-color:#2ecc71}.green:hover{background-color:#1abc9c}.img{filter:brightness(0) invert(1);width:20px;height:20px;margin-top:3px}